import {Component, HostBinding, Input, OnInit, EventEmitter, Output, Type, OnDestroy} from '@angular/core';
import {NavItem} from '../nav-item';
// import {NavService} from '../nav.service';
import {animate, state, style, transition, trigger} from '@angular/animations';
import { ComponentRouteService } from '../../shared/route.service';

@Component({
  selector: 'us-sidenav-list-item',
  template: `
    <mat-list-item [ngStyle]="{'padding-left': (depth * 1.5) + 'em'}" (click)="onItemSelected(item)"
                  [ngClass]="{'active': item.route ? router.isActive(item.route, true): false, 'expanded': expanded}">
      <mat-icon class="routeIcon"><small>{{item.iconName}}</small></mat-icon>
      {{item.displayName}}
      <span fxFlex *ngIf="item.children && item.children.length">
        <span fxFlex></span>
        <mat-icon [@indicatorRotate]=true> 
          <!-- "expanded ? 'expanded': 'collapsed'"> -->
          expand_more
        </mat-icon>
      </span>
    </mat-list-item>
    <div *ngIf="expanded">
      <us-sidenav-list-item *ngFor="let child of item.children" [item]="child" [depth]="depth+1">
      </us-sidenav-list-item>
    </div>
  `,
  styles: [`
    :host {
      display: block;
      outline: none;
    }
    :host .mat-list-item.active {
      background-color: cornflowerblue;
    }
    :host:hover > .mat-list-item:not(.expanded),
    :host:focus > .mat-list-item:not(.expanded) {
      background-color: blue;
    }
    .mat-list-item {
      padding: 8px 0;
    }
    .mat-list-item .routeIcon {
      margin-right: 40px;
    }
  `],
  animations: [
    trigger('indicatorRotate', [
      state('collapsed', style({transform: 'rotate(0deg)'})),
      state('expanded', style({transform: 'rotate(180deg)'})),
      transition('expanded <=> collapsed',
        animate('225ms cubic-bezier(0.4,0.0,0.2,1)')
      ),
    ])
  ]
})
export class SideNavListItemComponent implements OnInit {
  expanded: boolean;
  @HostBinding('attr.aria-expanded') ariaExpanded = this.expanded;
  @Input() item: NavItem;
  @Input() depth: number;
  // subscription: Subscription;

  constructor(
    private componentRouteService: ComponentRouteService) {
  }

  ngOnInit() {
    if (this.depth === undefined) {
      this.depth = 0;
    }
  }

  onItemSelected(item: NavItem) {
    console.log(item);
    if (!item.children || !item.children.length) {
      console.log(item.component);
      this.componentRouteService.updateNavItemAndComponent(item);
      // this.componentRouteService.updateNavItem(item);
      // this.componentRouteService.updateComponent(item.component);
      // this.router.navigate([item.route]);
      // this.navService.closeNav();
    }
    if (item.children && item.children.length) {
      this.expanded = !this.expanded;
    }
  }
}
